      border-radius: 50%;
    }

    .rese-h4 {
      font-size: 12px;
      font-family: vars.$font-stack;
      color: vars.$secondary-color;
      font-weight: vars.$font-weight;
    }
  }

  .promo {
    display: flex;
    flex-direction: column;
    align-items: center;

    .img {
      width: 66px;
      height: 66px;
      object-fit: cover;
      border: 4px solid #25292e;
      border-radius: 50%;
    }

    .promo-h4 {
      font-size: 12px;
      font-family: vars.$font-stack;
      color: vars.$secondary-color;
      font-weight: vars.$font-weight;
    }
  }

  .video {
    display: flex;
    flex-direction: column;
    align-items: center;

    .img {
      width: 66px;
      height: 66px;
      object-fit: cover;
      border-radius: 50%;
      border: 4px solid #25292e;
    }

    .video-h4 {
      color: vars.$secondary-color;
      font-size: 12px;
      font-family: vars.$font-stack;
      font-weight: vars.$font-weight;
    }
  }

  .desarro {
    display: flex;
    flex-direction: column;
    align-items: center;

    .img {
      width: 66px;
      height: 66px;
      object-fit: cover;
      border-radius: 50%;
      border: 4px solid #25292e;
    }

    .desarro-h4 {
      color: vars.$secondary-color;
      font-size: 12px;
      font-family: vars.$font-stack;
      font-weight: vars.$font-weight;
    }
  }

  .evento {
    display: flex;
    flex-direction: column;
    align-items: center;

    .img {
      width: 66px;
      height: 66px;
      object-fit: cover;
      border-radius: 50%;
      border: 4px solid #25292e;
    }

    .evento-h4 {
      color: vars.$secondary-color;
      font-size: 12px;
      font-family: vars.$font-stack;
      font-weight: vars.$font-weight;
    }
  }
}

.enla {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 8rem;
  padding-top: 20px;

  .publi {
    max-width: 193px;
  }

  .rel {
    max-width: 193px;
  }

  .perso {
    max-width: 193px;
  }

  .nela-a {
    text-decoration: none;
  }
}

